<template lang="pug">
    b-container
        section.admin
            h1.title.mb-3    EDMS
            b-row
                b-col
                    b-form(@submit.prevent="createGroup" class="mb-3")
                        h2.subtitle.mb-2   
                        b-form-group(
                            description="  ")
                            b-form-input(
                            type="text"
                            v-model="group.name"
                            required
                            placeholder="  ")
                        b-form-group(
                            label=" - :"
                            description="  ")
                            b-form-input(
                            type="text"
                            v-model="group.invite"
                            required
                            placeholder=" ")
                            b-button(type="button" @click="group.invite = generateInvite()" class="mt-3") 

                        b-form-group(
                            label=" - :"
                            description="  ")
                            b-form-input(
                                type="text"
                                v-model="group.adminInvite"
                                required
                                placeholder=" ")
                            b-button(type="button" @click="group.adminInvite = generateInvite()" class="mt-3") 
                        b-form-group(
                            label="  :"
                            description="  ")
                            b-form-input(
                                type="text"
                                v-model="group.adminLogin"
                                required
                                placeholder=" ")
                            b-button(type="button" @click="group.adminLogin = generateInvite()" class="mt-3") 
                            
                        b-button(type="submit" variant="primary")  

                b-col
                    h2.subtitle.mb-2  
                    b-alert(v-if="!groups.length" variant="warning" show)   ...
                    b-list-group(v-else)
                        b-list-group-item(v-for="group in groups" :key="group._id")
                            h3 {{ group.name }}
                            h4 : {{ group.users.length }}
                            b-list-group
                                b-alert(v-if="!group.users.length" variant="warning" show)   
                                b-list-group-item(
                                    v-for="user in group.users"
                                    :key="user._id"
                                    v-else)
                                    b-btn(v-if="user.role === 'Admin' && !user.author" @click="openSendModal(user, group)")  
                                    h4 {{ user.author ? user.author : ' ' }}
                                    small : {{ user.role ? user.role : ' ' }}
            b-row
                b-col
                    b-form(@submit.prevent="createAdmin" v-if="groupsOptions.length")
                        h2.subtitle   
                        b-form-group(
                                label=" :"
                                description=",    ")
                            b-form-select(
                                v-model="selectedGroup"
                                :options="groupsOptions"
                                class="mb-3")
                        b-form-group(
                            label="  :"
                            description="  ")
                            b-form-input(
                                type="text"
                                v-model="admin.login"
                                required
                                placeholder=" ")
                            b-button(type="button" @click="admin.login = generateInvite()" class="mt-3") 
                        b-form-group(
                            label=" - :"
                            description="  ")
                            b-form-input(
                                type="text"
                                v-model="admin.invite"
                                required
                                placeholder=" "
                                readonly)
                            b-button(type="button" @click="admin.invite = generateInvite()" class="mt-3") 
                        

                        b-button(type="submit" variant="primary")  

            b-modal(ref="modalSend" title="  " hide-footer)
                b-form(@submit.prevent="sendInvite")

                    b-form-group(label=":")
                        b-form-input(
                            type="text"
                            v-model="userForMail.login"
                            readonly)

                    b-form-group(label="-:")
                        b-form-input(
                            type="text"
                            v-model="userForMail.token"
                            readonly)

                    b-form-group(label=":" description="    !  !")
                        b-form-input(type="email" v-model="userForMail.email" required)

                    b-button(type="submit") 

</template>

<script>
import randomizer from '../modulesJs/randomizer';
import { mapGetters, mapActions } from 'vuex';
export default {
    data() {
        return {
            group: {
                name: '',
                invite: '',
                adminInvite: '',
                adminLogin: '',
            },
            admin: {
                invite: '',
                login: '',
            },
            selectedGroup: '',
            userForMail: {
                token: '',
                login: '',
                email: '',
            },
        }
    },
    computed: {
        ...mapGetters('groupsStore', ['groups']),
        groupsOptions() {
            let resultOptions = [{value: '', text: ' '}];
            this.groups.map(group => {
                resultOptions.push({ value: group._id, text: group.name });
            });

            return resultOptions;
        }
    },
    methods: {
        ...mapActions('groupsStore', ['getAllGroups', 'createNewGroup', 'createNewAdmin']),
        ...mapActions('usersStore', ['sendMail']),
        generateInvite(){
            return randomizer(5);
        },
        createGroup(e) {
            this.createNewGroup(this.group)
            .then(response => {
                e.target.reset();
                this.getAllGroups();
            });
        },
        createAdmin(e) {
            this.createNewAdmin({ invite: this.admin.invite, group: this.selectedGroup, role: 'Admin' })
            .then(response => {
                e.target.reset();
                this.getAllGroups();
            });
        },
        openSendModal(user, group) {
            this.userForMail = { ...user, subject: ` ""   ${group.name}` };
            this.$refs.modalSend.show();
        },
        sendInvite(e) {
            this.userForMail.groupInvite = this.group.invite;
            console.log(this.userForMail);
            this.sendMail(this.userForMail)
                .then(response => {
                    console.log(response);
                    e.target.reset();
                    this.$refs.modalSend.hide();
                })
                .catch(e = console.error(e));
        },
    },
    created() {
        this.getAllGroups();
    }
}
</script>
<style lang="sass" scoped>
.admin
    padding: 40px 0
ol
    list-style: inside
</style>




