<template lang="pug">
    b-container
        section.admin
            h1.title   {{ group.name }}
            b-row
                b-col
                    b-form(@submit.prevent="createUser" class="mb-3")
                        h2.subtitle  
                        b-form-group(
                            description="   "
                            )
                            b-form-input(
                                type="text"
                                v-model="user.role"
                                required
                                placeholder="")

                        b-form-group(
                            title="  "
                            label="  :"
                            description="  ")
                            b-form-input(
                                type="text"
                                v-model="user.login"
                                required
                                placeholder=" ")
                            b-button(type="button" @click="user.login = generateInvite()" class="mt-3") 

                        b-form-group(
                            description=" - "
                            )
                            b-form-input(
                                type="text"
                                v-model="user.invite"
                                required
                                readonly
                                placeholder="-")
                            b-button(type="button" @click="user.invite = generateInvite()" class="mt-3") 

                        b-button(type="submit") 
                b-col
                    h2.subtitle   {{ group.name }}
                    b-list-group.users-list
                        b-list-group-item(v-for="urrUser in users" :key="urrUser._id")
                            b-btn(v-if="urrUser.role !== 'Admin'" @click="openSendModal(urrUser)")  
                            h4 {{ urrUser.author ? urrUser.author : ' ' }}
                            small : {{ urrUser.role }}

            b-modal(ref="modalSend" title="  " hide-footer)
                b-form(@submit.prevent="sendInvite")

                    b-form-group(label=":")
                        b-form-input(
                            type="text"
                            v-model="userForMail.login"
                            readonly)

                    b-form-group(label="-:")
                        b-form-input(
                            type="text"
                            v-model="userForMail.token"
                            readonly)

                    b-form-group(label=":" description="    !  !")
                        b-form-input(type="email" v-model="userForMail.email" required)

                    b-button(type="submit") 

</template>

<script>
import randomizer from "../modulesJs/randomizer";
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      user: {
        role: "",
        invite: "",
        login: ""
      },
      userForMail: {
        token: "",
        login: "",
        email: ""
      }
    };
  },
  computed: {
    ...mapGetters("groupsStore", ["groups"]),
    ...mapGetters('usersStore', ['currentUser']),
    users() {
      return this.groups[0].users;
    },
    group() {
        return this.groups[0];
    }
  },
  methods: {
    ...mapActions("groupsStore", ["createNewUser", "getCurrentGroup"]),
    ...mapActions("usersStore", ["sendMail", "getCurrentUser"]),
    generateInvite() {
      return randomizer(5);
    },
    createUser(e) {
      this.createNewUser({ ...this.user, group: this.group._id })
        .then(response => {
          e.target.reset();
          this.getCurrentGroup(this.group.groupInvite);
        })
        .catch(e => console.log(e));
    },
    openSendModal(user) {
      this.userForMail = { ...user, groupInvite: this.group.groupInvite, subject: ` "${user.role}"   ${this.group.name}` };
      this.$refs.modalSend.show();
    },
    sendInvite(e) {
      console.log(this.userForMail);
      this.sendMail(this.userForMail)
        .then(response => {
          console.log(response);
          e.target.reset();
          this.$refs.modalSend.hide();
        })
        .catch((e = console.error(e)));
    }
  },
  created() {
    this.getCurrentUser().then(() => {
        console.log(this.currentUser);
        this.getCurrentGroup(this.currentUser.groupInvite);
    });
  }
};
</script>
<style lang="sass" scoped>
.admin
    padding: 40px 0
.users-list
    max-height: 445px
    overflow-y: scroll
    overflow-x: hidden
ol
    list-style: inside
</style>



